<template>
  <div class="wrapper">
    <div class="w-seach" @click="handlePopShow">
      <p :title="curName.length>6?curName:''">{{curName}}</p>
    </div>
    <transition name="fade">
      <depSeach v-if="popShow" :name="curName" @change="popClick"></depSeach>
    </transition>

    <div class="content">
      <div class="c-top">
        <div class="part ct-left">
          <ctitle :title="'信访部门信访走势'"></ctitle>
          <div class="inner">
            <pictorialBar class="chart" :params="xfqsBarParams"></pictorialBar>
          </div>
        </div>
        <div class="part ct-right">
          <ctitle :title="'投诉问题占比'"></ctitle>
          <div class="inner">
            <loopPie class="chart" :params="tswtzbPieParams"></loopPie>
          </div>
        </div>
      </div>
      <div class="c-btm">
        <div class="part">
          <ctitle :title="'人数规模占比'"></ctitle>
          <div class="inner">
            <loopPie class="chart" :params="rsgmzbPieParams"></loopPie>
          </div>
        </div>
        <div class="part">
          <ctitle :title="'重复访次数占比'"></ctitle>
          <div class="inner">
            <loopPie class="chart" :params="cffcszbPieParams"></loopPie>
          </div>
        </div>
        <div class="part">
          <ctitle :title="'走访形式占比'"></ctitle>
          <div class="inner">
            <loopPie class="chart" :params="zfxszbPieParams"></loopPie>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import {get_http} from "@/api/index";
  import depSeach from "@/components/depSeach/index"
  import ctitle from "@/components/title/index";
  import loopPie from "@/components/pie/LoopPie";
  import pictorialBar from "@/components/bar/pictorialBar";

  export default {
    name: "xfbm",
    components: {
      depSeach,
      ctitle,
      loopPie,
      pictorialBar
    },
    data() {
      return {
        popShow:false,
        curName:"部门索引",
        // 信访走势
        xfqsBarParams:{
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          data: [],
          options: {
            splitNumber:6,
            valueKey: "value",
            circleShow: true,
            symbol: 'path://d="M 0 0 15 0 8 108 0 0z"',
            symbolSize: [20, '100%'],
            barColor: "#ffc07d",
            top:"12%",
            bottom:"20%",
            left:"10%",
            right:"5%",
            splitLineShow:true,
            axisCategoryName:"月",
            axisYName: "件次",
          }
        },
        // 投诉问题占比
        tswtzbPieParams: {
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          data: [],
          options: {
            bgSize: 220,
            colors: this.$STANDARD.pieColors,
            radius: ["45%", "55%"],
            center: ["50%", "50%"],
            centerRadius: ["40%", "41%"],
            labelRich: {
              b: this.fontSize(0.18),
              d: this.fontSize(0.2),
              bHeight: this.fontSize(0.3)
            },
            length: 20,
            length2: 70,
            // borderColor: "#050f28",
            itemLabelShow: true, //数据扇区label是否显示
            //图例
            legend: {
              show: false
            },
            rich: {
              bfontsize: "16%" //文字大小
            },
            labelSize: "24%", // label字号
            rollFlag: true,
            threeLevel: true,
          }
        },
        // 人数规模占比
        rsgmzbPieParams:{
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          data: [],
          options: {
            colors: this.$STANDARD.gradientColors,
            radius: ["50%", "65%"],
            center: ["50%", "50%"],
            centerRadius: ["42%", "43%"],
            labelRich: {
              b: this.fontSize(0.2),
              d: this.fontSize(0.22),
              bHeight: this.fontSize(0.3)
            },
            length: 20,
            length2: 45,
            borderWidth: this.fontSize(0.05),
            bRichPadding: [30, -45, 0, -45],
            tooltip: {
              show: true
            },
            itemLabelShow: true, //数据扇区label是否显示
            //图例
            legend: {
              show: false
            },
            rich: {
              bfontsize: "14%" //文字大小
            },
            labelSize: "22%", // label字号
            rollFlag: true
          }
        },
        // 重复访次数占比
        cffcszbPieParams:{
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          data: [],
          options: {
            colors: this.$STANDARD.gradientColors,
            radius: ["50%", "65%"],
            center: ["50%", "50%"],
            centerRadius: ["42%", "43%"],
            labelRich: {
              b: this.fontSize(0.2),
              d: this.fontSize(0.22),
              bHeight: this.fontSize(0.3)
            },
            length: 20,
            length2: 45,
            borderWidth: this.fontSize(0.05),
            bRichPadding: [30, -45, 0, -45],
            tooltip: {
              show: true
            },
            itemLabelShow: true, //数据扇区label是否显示
            //图例
            legend: {
              show: false
            },
            rich: {
              bfontsize: "14%" //文字大小
            },
            labelSize: "22%", // label字号
            rollFlag: true
          }
        },
        // 走访形式占比
        zfxszbPieParams: {
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          data: [],
          options: {
            bgSize: 220,
            colors: this.$STANDARD.gradientColors,
            radius: ["45%", "55%"],
            center: ["50%", "50%"],
            centerRadius: ["40%", "41%"],
            labelRich: {
              b: this.fontSize(0.18),
              d: this.fontSize(0.2),
              bHeight: this.fontSize(0.3)
            },
            length: 20,
            length2: 70,
            // borderColor: "#050f28",
            itemLabelShow: true, //数据扇区label是否显示
            //图例
            legend: {
              show: false
            },
            rich: {
              bfontsize: "16%" //文字大小
            },
            labelSize: "24%", // label字号
            rollFlag: true,
            threeLevel: true,
          }
        }
      }
    },
    mounted() {
      this.getAPI(this.year, this.curName);
    },
    methods: {
      handlePopShow() {
        this.popShow = true;
      },
      // 点击弹框后触发
      popClick(name){
        this.curName=(name === ''?'部门索引':name);
        this.getAPI(this.year, this.curName);
      },
      getAPI(year,xfbm) {
        let xfbmParam=(xfbm === '部门索引' ? 'drop' : xfbm);
        // 1.信访走势
        get_http("zfxwXfbmXfzsBar", {xfbm: xfbmParam, year: year})
          .then(res => {
            this.xfqsBarParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
        // 2.投诉问题占比
        get_http("zfxwXfbmTswtzbPie", {xfbm: xfbmParam, year: year})
          .then(res => {
            this.tswtzbPieParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
        // 3.人数规模占比
        get_http("zfxwXfbmRsgmzbPie", {xfbm: xfbmParam, year: year})
          .then(res => {
            this.rsgmzbPieParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
        // 4.重复访次数占比
        get_http("zfxwXfbmCffcszbPie", {xfbm: xfbmParam, year: year})
          .then(res => {
            this.cffcszbPieParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
        // 5.走访形式占比
        get_http("zfxwXfbmZfxszbPie", {xfbm: xfbmParam, year: year})
          .then(res => {
            this.zfxszbPieParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
      },
      fontSize(res) {
        let docEl = document.documentElement,
          clientWidth =
            window.innerWidth ||
            document.documentElement.clientWidth ||
            document.body.clientWidth;
        if (!clientWidth) return;
        let fontSize = 100 * (clientWidth / 1920);
        return res * fontSize;
      }
    },
    computed: {
      year() {
        return this.$store.state.year;
      }
    },
    watch: {
      year(val) {
        this.getAPI(val, this.curName);
      }
    }
  }
</script>

<style lang="stylus" scoped>
  .wrapper
    position relative
    top 0.8rem
    height calc(100% - 0.8rem)
    .fade-enter-active, .fade-leave-active
      transition opacity .3s
    .fade-enter, .fade-leave-to
      opacity 0
    .w-seach
      position absolute
      top -78px
      right 113px
      z-index 1000
      box-sizing border-box
      padding-left 42px
      width 262px
      line-height 62px
      font-size 22px
      color #cbfbff
      background url("../../../assets/seach-bg.png") no-repeat
      cursor pointer
      p
        width 150px
        text-align center
        white-space nowrap
        overflow hidden
        text-overflow ellipsis
    .content
      position relative
      z-index 1
      width 100%
      height 100%
      display flex
      flex-direction column
      padding .2rem .3rem
      box-sizing border-box
      .part
        display flex
        flex-direction column
        width 100%
        height 100%
        .inner
          display flex

          height calc(100% - 0.28rem)
          .chart
            width 100%
            height 100%
      .c-top
        flex 1
        display flex
        .ct-left
          flex 2
        .ct-right
          flex 1
      .c-btm
        flex 1
        display flex
        .part
          flex 1
</style>
