<!--
  @auth:lcw
  象形柱图
-->
<template>
  <div
    v-loading="!params.data.length"
    :element-loading-background="$STANDARD.loadingBgColor"
    ref="chartEl"
    :style="{width : params.canvasStyle.width, height: params.canvasStyle.height}"
  ></div>
</template>

<script>
  import * as echarts from 'echarts';
  import {gradientColor} from "@/utils/gradient";

  export default {
    name: "pictorialBar",
    props: {
      animateFlag: false,
      params: {
        type: Object,
        default() {
          return {
            canvasStyle: {
              width: "1000px",
              height: "600px"
            },
            options: {
              barColors: this.$STANDARD.barColors,
              labelCut: true,
            },
            data: []
          }
        }
      }
    },
    data() {
      return {
        myChart: null,
      };
    },
    mounted() {
      this.init();
      this.$once('hook:beforeDestroy', () => {
        if (this.interval) {
          clearInterval(this.interval);
        }
        this.myChart && this.myChart.dispose();
      })
    },
    methods: {
      init() {
        if (this.myChart) {
          this.myChart.dispose();
          this.myChart = null;
        }
        if (this.params.data.length === 0) {
          return;
        }
        this.myChart = echarts.init(this.$refs.chartEl);
        let options = this.params.options;
        let categoryData = [], valueData = [];
        this.params.data.forEach((item) => {
          categoryData.push(item.name);
          valueData.push(item[this.params.options.valueKey ? this.params.options.valueKey : "value"]);
        });

        let option = {
          tooltip: {
            trigger: "axis",
            backgroundColor: this.$STANDARD.tooltipBackgroundColor,
            padding: [10, 20],
            axisPointer: {
              // 坐标轴指示器，坐标轴触发有效
              type: "shadow" // 默认为直线，可选为：'line' | 'shadow'
            },
            formatter: function (params) {
              if (options.axisCategoryName) {
                return params[0].name + options.axisCategoryName + "<br/>" + params[0].value.toLocaleString();
              } else {
                return params[0].name + ' : ' + params[0].value.toLocaleString();
              }
            }
          },
          // legend: {
          //   show: true,
          //   selectedMode: false,
          //   top: '2%',
          //   right: '4%',
          //   textStyle: {
          //     color: '#fff',
          //     fontSize: 16
          //   }
          // },
          grid: {
            top: options.top ? options.top : "5%",
            bottom: options.bottom ? options.bottom : "5%",
            left: options.left ? options.left : "5%",
            right: options.right ? options.right : "5%",
          },
          xAxis: {
            type: 'category',
            name: options.axisCategoryName ? options.axisCategoryName : "",
            nameLocation: options.nameLocation ? "start" : "end",
            nameTextStyle: {
              color: options.XaxisNameColor ? options.XaxisNameColor : "#fff",
              fontSize: options.axisLabelSize ? options.axisLabelSize : 20
            },
            data: categoryData,
            axisLine: {
              show: true,
              lineStyle: {
                width: 2,
                color: this.$STANDARD.xAxisLineColor
              }
            },
            axisTick: {
              alignWithLabel: true,
              //show: false,//x轴刻度是否显示
              show: options.axisTickShow ? true : false,
              inside: true //x轴刻度是否朝内
            },
            axisLabel: {
              interval: 0,
              margin: 10,
              fontSize: 16,
              color: this.$STANDARD.xAxisColor,
              // 坐标label垂直显示
              // formatter: (params) => {
              //   return this.params.data.length <= 5 ? params : params.split('').join('\n')
              // }
            },
            splitLine: {
              show: false
            }
          },
          yAxis: {
            type: 'value',
            name: options.axisYName ? options.axisYName : "",
            nameLocation: options.nameLocation ? "start" : "end",
            nameTextStyle: {
              color: options.YaxisNameColor ? options.YaxisNameColor : "#ff517f",
              fontSize: options.axisLabelSize ? options.axisLabelSize : 14,
              fontWeight:'bold',
              padding: [0, 45, 0,0]
            },
            splitNumber: options.splitNumber ? options.splitNumber : 5,
            axisLabel: {
              show: options.YaxisaxisLabelShow ? false : true,
              margin: 10,
              fontSize: 14,
              color: (value, index) => {
                let arr=[];
                let num=options.splitNumber ? options.splitNumber : 5;
                if(index<num){
                  for(let i=0;i<num;i++){
                    arr.push(0)
                  }
                  let gradientC = gradientColor(arr, "#fdc48d", "#ff517f");
                  return gradientC(index)
                }else{
                  return "#ff517f"
                }
              },
            },
            axisLine: {
              show: options.YaxisLineShow ? true : false,
              width: 2,
              type: "dotted",
              color: this.$STANDARD.yAxisSplitLineColor
            },
            axisTick: {
              show: false
            },
            splitLine: {
              show: options.splitLineShow ? true : false,
              lineStyle: {
                type: 'dashed',
                color: "#2c344c" //网格线颜色（所有图表需要统一）
              }
            }
          },
          series: [
            {
              name: '数量',
              type: 'pictorialBar',
              symbol: this.params.options.symbol,
              // symbol:'path://d="M0,455s114-80,170-235S218,0,253,0s52,165,82,219c0,0,63,164,171,236Z"',
              symbolSize: this.params.options.symbolSize,
              itemStyle: {
                color: this.params.options.gradient ?
                  new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                    offset: 0,
                    color: this.params.options.gradient[0]
                  }, {
                    offset: 1,
                    color: this.params.options.gradient[1]
                  }])
                  : this.params.options.barColor
              },
              data: valueData,
            },
            {
              name: '圆圈',
              type: 'pictorialBar',
              symbolSize: this.params.options.circleShow ? [30, 30] : [0, 0],
              symbolOffset: [0, -10],
              z: 12,
              symbolPosition: 'end',
              itemStyle: {
                color: this.params.options.barColor
              },
              data: valueData,
            },
          ]
        };

        this.myChart.setOption(option);
      }
    },
    watch: {
      animateFlag: {
        handler(newVal, oldVal) {
          this.init();
        },
        deep: true
      },
      'params.data': {
        handler(newVal, oldVal) {
          this.init();
        },
        deep: true
      }
    }
  }
</script>

